body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  transition: 0.3s;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#movies img,
#movie img {
  width: 100%;
}
.brand-text {
  font-size: 2rem;
}
#react-logo {
  color: #00d8ff;
}
#imdb-logo {
  color: #f5de50;
}
@media (min-width: 960px) {
  #movies .col-md-3 .well {
    height: 390px;
  }

  #movies .col-md-3 img {
    height: 240px;
  }
}

.darkmode{
  background-color: black;
  color: white;
  transition: 0.3s;
}

.darkjumbotron{
  background-color: #454545;
  transition: 0.3s;
}

.darkmoviedetail, li{
  background-color: #454545;
  color: black
}

#darkmodebox{
  -webkit-appearance: none;
  -moz-appearance: none;
       appearance: none;
  background-color: black;
  height: 25px;
  width: 50px;
  border: 2px solid white;
  border-radius: 50px;
  position: absolute;
  cursor: pointer;
  outline: none;
  transition: 0.3s;
}

#darkmodebox:before{
  content: "";
  position: absolute;
  height: 17px;
  width: 17px;
  border-radius: 50px;
  background-color: white;
  top: 2px;
  left: 2.5px;
  transition: 0.3s;
}
#darkmodebox:checked{
  background-color: white;
  border: 2px solid black;
}
#darkmodebox:checked::before{
  background-color: black;
  left: 26px;
}
